import React from 'react'
import Form from 'react-bootstrap/Form';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button'
import Axios from 'axios';

export default class ContactForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            nombre: '',
            apellido: '',
            email: '',
            servicio: '',
            mensaje: ''
        }
    }

    render() {
        return (
            <div className="form-container">
                <Form id="contact-form" onSubmit={this.handleSubmit.bind(this)} method="POST">
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridNombre">
                            <Form.Label>Nombre</Form.Label>
                            <Form.Control
                                placeholder="Nombre"
                                value={this.state.nombre}
                                onChange={this.onNombreChange.bind(this)}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridApellido">
                            <Form.Label>Apellido</Form.Label>
                            <Form.Control
                                placeholder="Apellido"
                                value={this.state.apellido}
                                onChange={this.onApellidoChange.bind(this)}
                            />
                        </Form.Group>
                    </Form.Row>

                    <Form.Group controlId="formGridEmail">
                        <Form.Label>Email</Form.Label>
                        <Form.Control
                            aria-describedby="emailHelp"
                            type="email"
                            placeholder="ejemplo@email.com"
                            value={this.state.email}
                            onChange={this.onEmailChange.bind(this)}
                        />
                    </Form.Group>

                    <Form.Group controlId="formGridAServicio">
                        <Form.Label>Servicio</Form.Label>
                        <Form.Control
                            placeholder="Servicios que desea consultar"
                            value={this.state.servicio}
                            onChange={this.onServicioChange.bind(this)}
                        />
                    </Form.Group>

                    <Form.Group controlId="formGridMensaje">
                        <Form.Label>Mensaje</Form.Label>
                        <Form.Control
                            as="textarea"
                            rows="3"
                            placeholder="Ingrese un mensaje"
                            value={this.state.mensaje}
                            onChange={this.onMensajeChange.bind(this)}
                        />
                    </Form.Group>

                    <Button type="submit">
                        Enviar
                </Button>
                </Form>
            </div>
        )
    };

    onNombreChange(event) {
        this.setState({ nombre: event.target.value })
    }

    onApellidoChange(event) {
        this.setState({ apellido: event.target.value })
    }

    onEmailChange(event) {
        this.setState({ email: event.target.value })
    }

    onServicioChange(event) {
        this.setState({ servicio: event.target.value })
    }

    onMensajeChange(event) {
        this.setState({ mensaje: event.target.value })
    }

    resetForm(){
    
        this.setState({ nombre: "", apellido: "", email: "", servicio: "", mensaje: "" })
     }


    handleSubmit(e){
        e.preventDefault();
    
        Axios({
          method: "POST", 
          url:"http://localhost:3000/send", 
          data:  this.state
        }).then((response)=>{
          if (response.data.status === 'success'){
            alert("Mensaje Enviado."); 
            this.resetForm()
          }else if(response.data.status === 'fail'){
            alert("Fallo envio de mensaje")
          }
        })
      }
}